<script>
    import {createEventDispatcher, getContext, onDestroy, setContext} from 'svelte';
    import { json } from "d3";
    import L from 'leaflet';

    import EventBridge from '../lib/EventBridge';

    const {getMap} = getContext(L);

    export let url = null;
    export let geoData = null;
    export let options = {};
    export let events = [];

    let geojson;

    setContext(L.Layer, {
        getLayer: () => geojson
    });

    const dispatch = createEventDispatcher();
    let eventBridge;

    $: {
        if (!geojson) {
            geojson = L.geoJSON(null, options).addTo(getMap());
            eventBridge = new EventBridge(geojson, dispatch, events);
        }
        if(url){
            json(url).then(function(data) {
                geojson.clearLayers();
                geojson.addData(data.features);
            });
        }
        if(geoData){
            geojson.clearLayers();
            geojson.addData([geoData]);
        }

    }

    onDestroy(() => {
        eventBridge.unregister();
        geojson.removeFrom(getMap());
    });

    export function getGeoJSON() {
        return geojson;
    }
</script>

<div>
    {#if geojson}
        <slot/>
    {/if}
</div>
